@font-face {
  font-family: Avenir;
  src: url(fonts/Avenir-Book.ttf);
  font-style: normal;
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url(fonts/Avenir-Heavy.ttf);
  font-style: normal;
  font-weight: 900;
}

.searchBarForm {
  margin-top: 20px;
  position: relative;

}

.searchBarBig {
  box-sizing: border-box;
  font-family: Avenir, sans-serif;
  width: 100%;
  height: 70px;
  border-radius: 4px;
  color: #3d4e61;
  background-color: #fff;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  font-size: 16px;
  line-height: 22px;
  padding: 24px 62px;
}

.bigImage {
  position: absolute;
  top: 25px;
  left: 25px;
}

.searchBarSmall {
  height: 42px;
  font-size: 14px;
  line-height: 19px;
  padding: 11px 33px;
}

.smallImage {
  position: absolute;
  top: 15px;
  left: 13px;
  width: 11px;
}

.searchBarBig:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.searchBarBig:focus {
  font-weight: 900;
  background-color: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  border: 1px solid #e1e7ed;
  outline: none;
}

.searchBarBig::placeholder,
.searchBarSmall::placeholder {
  font-weight: 300;
}
